import React, { Component } from "react";
import cssClass from "./address-modal.module.css";
import Modal from "react-modal";
import { Link } from "react-router-dom";
import InputText from '../../admin/views/TextFields/InputField';
class AddressModal extends Component {
    state = {
        fullname: "",
        mobileNumber: "",
        alternateNumber: "",
        pinCode: "",
        address: "",
        locality: "",
        landmark: "",
        states: "",
        city: "",
    };
    
    changeHandler = (e) => {
        const { name, value } = e.target;
        
        this.setState({
            [name]: value,
        });
    };
    
    onSubmit = (e) => {
        e.preventDefault();
        alert('hello')
    };
    render() {
    const { isOpen, onRequestClose } = this.props;
    return (
      <div>
        <Modal
          className={cssClass.AddressModal}
          isOpen={isOpen}
          onRequestClose={onRequestClose}
          // overlayClassName={cssClass.AddressOverlay}
          style={{
            overlay: { background: "rgba(0,0,0,.5)" },
            content: {},
          }}
        >
            <h6 className="pl-3"> Add New Address</h6>
            <hr />
          {/* <div className={cssClass.CloseIcon}></div> */}
          <div className={cssClass.modalBody}>
          <form onSubmit={this.submitHandler}>
            <div className={` ${cssClass.AddressInputText}`}>
              <div className="container">
                <h5 className="mt-1">Delivery Info</h5>
                <p style={{ borderBottom: "2px solid aqua", width: "10%" }}></p>
                <div className="row">
                  <div className="col-sm-12 col-12">
                    <div className="row">
                    <div className="col-sm-6">
                      <InputText label="First Name"/>
                    </div>
                    <div className="col-sm-6">
                      <InputText label="Last Name"/>
                    </div>
                    </div>
                  </div>
                 
                  <div
                    className="col-sm-12 col-12 col-xs-12"
                  > 
                    <InputText
                      label="Mobile Number"
                      onChange={this.changeHandler}
                     
                    />
                  </div>
                </div>
              </div>
              <div className="container-fluid">
                <div>
                  <h5 className=" mt-1">Address info</h5>
                  <p
                    style={{ borderBottom: "2px solid aqua", width: "10%" }}
                  ></p>
                </div>
                <div className="row">
                  
                  <div className="col-sm-12 col-xs-12">
                    <InputText
                      type="text"
                      name="address"
                      placeholder="Address"
                      // value={address.value}
                      onChange={this.changeHandler}
                    />
                    <InputText
                      type="text"
                      name="locality"
                      placeholder="Locality"
                      // value={locality.value}
                      onChange={this.changeHandler}
                    />
                    <InputText
                      type="text"
                      name="landmark"
                      placeholder="Landmark(Optional)"
                      // value={landmark.value}
                      onChange={this.changeHandler}
                    />
                  </div>
                  <div
                    className={`col-sm-12 col-xs-12 d-flex justify-content-between ${cssClass.AddressStateCity}`}
                  >
                    <InputText
                      type="tel"
                      name="pinCode"
                      maxLength="6"
                      placeholder="Pin Code"
                      // value={pinCode.value}
                      onChange={this.changeHandler}
                      className="col-sm-5 col-xs-5"
                    />
                    <InputText
                      type="text"
                      name="city"
                      placeholder="City"
                      // value={city.value}
                      onChange={this.changeHandler}
                      className="col-sm-5 col-xs-5"
                    />
                  </div>
                  <div
                    className={`col-sm-12 col-xs-6 d-flex justify-content-between ${cssClass.AddressCountry}`}
                  >
                    <InputText
                      type="text"
                      name="states"
                      placeholder="State"
                      // value={states.value}
                      onChange={this.changeHandler}
                      className="col-sm-5 col-xs-5"
                    />
                    <InputText
                      type="text"
                      placeholder="Country"
                      required
                      defaultValue="India"
                      className='col-sm-5 col-xs-5'
                    />
                  </div>
                </div>
              </div>

              <div className={` ${cssClass.AddressSaveCancelButton}`}>
                <div className="d-flex justify-content-center align-items-center">
                  <div className="">
                      <button onClick={this.onSubmit} type="submit" className={cssClass.SaveButton}>
                        SAVE
                      </button>
                  </div>

                  <div className="">
                    <button
                      type="reset"
                      className={cssClass.CancelButton}
                      onClick={onRequestClose}
                    >
                      CANCEL
                    </button>
                  </div>
                </div>
              </div>
            </div>
          </form>
          </div>
        </Modal>
      </div>
    );
  }
}

export default AddressModal;